.section-pricing{
    padding: 9.6rem 0;;
}
.mg-bottom{
    margin-bottom: 4.8rem !important;
}

.list{
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 1.8rem;
}
.list-item{
    display: flex;
    align-items: center;
    gap:1.6rem;
    font-size: 1.8rem;
    line-height: 1.2;
}
.list-icon{
    width: 3rem;
    height: 100%;
    stroke: #e67e22;
}
.plan-header{
    text-align: center;
    margin-bottom: 4.8rem;
}
.pricing-plan{
    border-radius: 1.1rem;
    width: 75%;
}
.starter{
    padding: 4.6rem;
    justify-self: end;
    border: 0.2rem solid #fdf2e9;
}
.complete{
    padding: 4.8rem;
    background-color: #fdf2e9;
    position: relative;
    overflow: hidden;
}
.complete::after{
    content: "best value";
    position: absolute;
    top: 6%;
    right: -10%;
    text-transform: uppercase;
    font-size: 1.4rem;
    font-weight: 700;
    color: #333;
    padding: 0.8rem 4.2rem;
    background-color: #ffd43b;
    transform: rotate(45deg);
    
}
.plan-name{
    color: #cf711f;
    font-weight: 600;
    font-size: 2rem;
    text-transform: uppercase;
    letter-spacing: 0.75;
    margin-bottom: 3.2rem;

}
.plan-price{
    font-size: 6.2rem;
    font-weight: 600;
    color: #333;
    margin-bottom: 1.6rem;

}
.plan-price span{
    font-size: 3rem;
    font-weight: 500;
    margin-right: 0.8rem;
}
.plan-text{
    font-size: 1.6rem;
    line-height: 1.6;
    color:#6f6f6f

}
.plan-sign-up{
    text-align: center;
    margin-top: 4.8rem;
}
.plan-details{
    font-size: 1.6rem;
    line-height: 1.6;
    text-align: center;
}

.grid:not(:last-child){
    margin-bottom: 9.6rem;
    
}
.feature-icon{
    stroke: #e67e22;
    height: 5.5rem;
    width: 5.5rem;
    margin-bottom: 3.2rem;
    background-color: #fdf2e9;
    padding: 1.2rem;
    border-radius: 50%;
    

}
.feature-title{
    font-size:2.4rem;
    color: #333;
    font-weight: 700;
    margin-bottom: 1.6rem;


}
.feature-text{
    font-size:1.8rem ;
    line-height: 1.8;

}