header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fdf2e9;

    /* სიმაღლე იმიტომ მივუთითეთ,რომ სამომავლოდ დაგვჭირდება ჰედერის გასტიკვა,ანუ სქროლზე რო ჩამოგვყვეს ქვემოთ */
    height: 9.6rem;
    padding: 0 4.8rem;
    position: relative;


}
.logo{
    height:2.2rem;
}
.main-nav-list{
    list-style: none;
    display: flex;
    align-items: center;
    gap: 4.8rem;
}
.main-nav-link:link,
.main-nav-link:visited{
    display: inline-block;
    text-decoration: none;
    color: #333;
    font-weight: 500;
    font-size: 1.8rem;
    transition: all 0.3s;
}
.main-nav-link:hover,
.main-nav-link:active{
    color:#cf711f
}

.main-nav-link.nav-cta:link,
.main-nav-link.nav-cta:visited{
    padding: 1.2rem 2.4rem;
    border-radius: 9px;
    color: #fff;
    background-color: #e67e22;
}
.main-nav-link.nav-cta:hover,
.main-nav-link.nav-cta:active{
    background-color: #cf711f;
}

/* ------------------MOBILE-------------- */
.btn-mobile-nav{
    border: none;
    background-color: rgb(253, 242, 233);
    cursor: pointer;
    display: none;

}
.icon-mobile-nav{
    background-color: none;
    height: 4.8rem;
    width: 4.8rem;
    stroke: #333 !important;
}
.icon-mobile-nav[name="close-outline"]{
    display: none;
}
