.footer{
    padding: 9.6rem 0;
    border-top: 1px solid #ddd;
}
.grid--5-cols{
    display: grid;
    grid-template-columns: 1.5fr 1.5fr 1fr 1fr 1fr;
}
.footer-heading{
    font-size: 1.8rem;
    font-weight: 500;
    margin-bottom: 4rem;
}
.footer-logo{
    display: block;
    margin-bottom: 3.2rem;
}
.footer-nav{
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 2.4rem;

}
.footer-link:link,
.footer-link:visited{
    text-decoration: none;
    font-size: 1.6rem ;
    color: #767676;
    transition: all 0.3s;
}
.footer-link:hover,
.footer-link:active{
    color: #555;

}
.socila-links{
    list-style: none;
    display: flex;
    gap: 2.4rem;
}
.socila-icon{
    height: 2.4rem;
    width: 2.4rem;
}
.copyright{
    font-size: 1.4rem;
    color: #767676;
    line-height: 1.6;
    margin-top: auto;
}
.logo-col{
    display: flex;
    flex-direction: column;
    
}
.contacts{
    font-style: normal ;
    font-size: 1.6rem;
    line-height: 1.6;
}
.address{
    margin-bottom: 2.4rem;
}