.grid{
    display: grid;
    column-gap: 6.4rem;
    row-gap: 9.6rem;
}
.grid--3-cols{
    grid-template-columns: repeat(3,1fr);
}
.section-meals{
    padding: 9.6rem 0;
}
.meal-img{
    width: 100%;
}
.meal{
    box-shadow: 0 2.4rem 4.8rem rgba(0,0,0,0.075);
    border-radius: 1.1rem;
    overflow: hidden;
    transition: all 0.4s;
}
.meal:hover{
    transform:translateY(-1.2rem);
    box-shadow: 0 3.2rem 6.4rem rgba(0,0,0,0.06);
}
.meal-content{
    padding: 3.2rem 4.8rem 4.8rem 4.8rem;
}
.meal-tags{
    margin-bottom: 1.2rem;
    display: flex;
    gap:0.4rem
}
.tag{
    display: inline-block;
    padding: 0.4rem 0.8rem;
    font-size: 1.2rem;
    text-transform: uppercase;
    color: #333;
    border-radius: 10rem;
    font-weight: 600;
}
.tag-vegetarian{
    background-color: #51cf66;
}
.tag-vegan{
    background-color: #94d82d;
}
.tag-paleo{
    background-color: #ffd43b;
}
.meal-title{
    font-size: 2.4rem;
    color: #333;
    font-weight: 600;
    margin-bottom: 3.2rem;

}
.meal-attributes{
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}
.meal-attribute {
    position: relative;
}
.meal-attribute span{
    margin-left: 3rem;
    display: flex;
    align-items: center;
    gap: 1.6rem;
    font-size: 1.8rem;
}
.icon{
    height: 2.4rem;
    width: 100%;
    stroke:#e67e22;
    position: absolute;
    top: 0;
    left: 0;;
    transform: translate(-47%,-25%);

}
.diet-tertriary{
    font-size: 3rem;
    font-weight: 700;
    margin-bottom: 1.6rem;
}
.list{
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 1.8rem;
}
.list-item{
    display: flex;
    align-items: center;
    gap:1.6rem;
}
.list-icon{
    width: 3rem;
    height: 100%;
    stroke: #e67e22;
}
.list-item {
    font-size: 1.8rem;
}
.mrg-bottom{
    margin-bottom: 4.8rem !important;
}
.all-recipes{
    font-size: 1.8rem;
    text-align: center;
}
.link:link,
.link:visited{
    display: inline-block;
    color: #e67e22;
    text-decoration: none;
    border-bottom: 0.1rem solid currentColor;
    padding: 0.2rem;
    transition: 0.3s;

}
.link:hover,
.link:active{
    color: #cf711f;
    border-bottom: 0.1rem solid transparent;

}
.text-center{
    text-align: center;
}
