/* 


---01 TYPOGRAPHY SYSTEM

-Font sizes (px)
10 / 12 / 14 / 16 / 18 / 20 / 24 / 30 / 36 / 44 / 52 / 62 / 74 / 86 / 98
1rem = 16px default მაგრამ HTML font-size თუ იქნება მაგ 10px მაშინ 1rem = 10px;

-Font weights
Default:400 
Medium: 500
Semi-bold: 600
Bold: 700 

-Line heights
Default: 1
Small: 1.05
Medium: 1.2
Paragraph default: 1.06

-Line spacing
-0.5px
0.75px


----------------------------------------------------------------------------------------

---02 COLORS 

- Primary: #e67e22
- Tints: #fdf2e9
- Shades:#cf711f
- Accents:
- Greys:
#555
#333


-------------------------------------------------------------------------------------------

---05 SHADOWS






-------------------------------------------------------------------------------------------

---06 BORDER-RADIUS
Default: 9px








-------------------------------------------------------------------------------------------

---07 WHITESPACE
-Spacing systems (px)
2 / 4 / 8 / 12 / 24 / 32 / 48 / 64 / 80 / 96 / 128


-------------------------------------------------------------------------------------------


*/
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;500;600;700&family=Rubik:wght@400;500;600;700&display=swap');

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
*:focus{
    outline: none;
    /* outline: 0.3rem dotted #e67e22;
    outline-offset: 0.8rem; */
    box-shadow: 0 0 0 0.8rem rgba(230,125,34,0.5);
}
html{
    font-size: 62.5%;
    overflow-x: hidden;
}

body{
    font-family: 'Rubik', sans-serif;
    line-height: 1;
    font-weight: 400;
    color: #555;
    overflow-x: hidden;
}

.margin-right-sm{
    margin-right: 1.6rem !important;
}


strong{
    font-weight: 500;
}


@media (max-width:84rem){
    .hero {
        max-width: 120rem !important;
    }
    .heading-primary{
        font-size: 4.4rem !important;
    }
    .gallery {
        grid-template-columns: repeat(2,1fr) !important;
    }
}

@media (max-width:75rem){
    html {
        font-size: 56.25% ;
    }
    .grid{
        column-gap: 4.8rem !important;
        row-gap: 6.4rem !important;
    }
    .heading-secondary {
        font-size: 3.6rem !important;
    }
    .heading-tertiary{
      font-size: 2.4rem !important;
    }
    .header{
        padding: 0 3.2rem !important;
    }
    .main-nav-list{
        gap:3.2rem !important
    }
    .hero{
        gap:4.8rem !important
    }
    .testimonial-container{
        padding: 9.6rem 3.2rem !important;
    }

}
@media(max-width:59rem){
  
    html {
        font-size: 50% ;
    }
    .hero{
        grid-template-columns: 1fr !important;
        padding: 0 8rem !important;
        gap: 6.4rem !important;
    }
    .hero-text-box,
    .hero-img-box{
        text-align: center !important;
    }
    .hero-img{
        width: 60% !important;
    }
    .delivered-meals{
        justify-content: center !important;
        margin-top: 3.2rem !important;
    }
    .logos img {
        height: 2.4rem !important;
    }
    .step-number{
        font-size: 7.4rem !important;
    }
    .meal-content {
        padding: 2.4rem 3.2rem 3.2rem 3.2rem !important ;
    }
    .section-testimonials{
        grid-template-columns: 1fr !important;
    }
    .gallery{
        grid-template-columns: repeat(6,1fr) !important;
    }
    .cta {
        grid-template-columns: 3fr 2fr !important;
    }
    .cta-form{
        grid-template-columns: 1fr !important;
    }
    .btn-form{
        margin-top: 1.2rem !important;
    }

    /* -------------MOBILE NAVIGATION------------ */
    .btn-mobile-nav{
        display: block !important;
        position: absolute;
        top: 40%;
        right: 5%;
        
        
        
        
    }
    .main-nav{
       
        background-color: rgba(253, 242, 233,0.97);
        position: absolute ;
        top:0 ;
        left:0 ;
        width: 100% ;
        height: 100vh ;
        transform:translateX(100%) ;
       
        
        
    
        display: flex ;
        align-items: center ;
        justify-content: center ;
        transition: all 1s ease-in !important;

        /* ---HIDE NAVIGATION--- */
        /* რაიმეს გასაქრობად opacity არ არის საკმარისი,რადგან მართალია ის ეკრანიდან ქრება მაგრამ თუ TAB ით გადავყვებით
        კლავიადურიდან მაინც მოინიშნება რადგანაც ის უბრალოდ გამჭვირვალეა თორე რეალურად არსებობს */
        opacity: 0;

        /* ამიტომაც დამატებით გვჭირდება შემდეგი რაღაც */
        /* მასზე ივენთი ვერ მოხდეს მაუსით ან კლავიატურით */
        pointer-events: none;
        
        /* სქრინ რიდერისათვის იყოს დაჰაიდებული */
        visibility: none;

    }
    /* header ში header კლასნეიმს თუ დავუმატებთ nav-open მაშინ გამოვა ნავიგაციის ბარი,
    ნუ ამას ჯავასკრიპტი უნდა და ბათონზე დაკლეკვით უნდა დავუმატოთ კლასნეიმი*/


    .nav-open .main-nav{
        opacity: 1 ;
        pointer-events: auto;
        visibility: visible;
        transform:translateX(0); 
    }
    .nav-open .icon-mobile-nav[name="close-outline"]{
        display: block !important;
    }
    .nav-open .icon-mobile-nav[name="menu-outline"]{
        display: none !important;
    }
    .main-nav-list{
        flex-direction: column ;
        gap:4.8rem !important;
    }
    .main-nav-link:link,
    .main-nav-link:visited{
        font-size: 3rem !important;
    }

}
@media (max-width: 44rem){
    .grid--3-cols,
    .grid--4-cols{
        grid-template-columns: repeat(2,1fr) !important;
    }
    .diets{
        grid-column: 1 / -1;
        justify-self: center;
    }

    .heading-secondary{
        margin-bottom: 4.8rem !important;
    }
    .pricing-plan{
        width: 100% !important;
    }
    .grid--5-cols{
        grid-template-columns: repeat(6,1fr) !important;
    }
    .logo-col,
    .address-col{
        grid-column: span 3 !important;

    }
    .nav-col{
        grid-row: 1 !important;
        grid-column: span 2 !important;
        margin-bottom: 3.2rem !important;
    }

}

@media (max-width:34rem){
    .grid {
        row-gap: 4.8rem !important;
    }
    .grid--2-cols,
    .grid--3-cols,
    .grid--4-cols{
        grid-template-columns: 1fr !important;
    }
    .section-hero{
        padding: 2.4rem 0 6.4rem 0 !important;
    }
    .hero{
        padding: 0 3.2rem !important;
    }
    .icon-mobile-nav{
        height: 3.2rem !important;
    }
    .btn,
    .btn:link,
    .btn:visited{
        padding: 1.8rem 1.6rem !important;
    }
    .hero-img{
        width: 80% !important;
    }
    .logos img{
        height: 1.4rem !important;
    }
    .step-img-box:nth-child(2){
        grid-row:1 ;

    }
    .step-img-box:nth-child(6){
        grid-row:5 ;

    }
    .step-img-box{
        transform: translateY(2.4rem) !important;
    }
    .testimonials{
        grid-template-columns: 1fr !important;
    }
    .gallery{
        grid-template-columns: repeat(4,1fr) !important;
        gap: 1.2rem !important;
    }
    .cta{
        grid-template-columns: 1fr !important;
    }
    .cta-text-box{
        padding: 3.2rem !important;
    }
    .cta-img-box{
        height: 32rem;
        grid-row: 1;
    }



}