.section-hero{
    background-color: #fdf2e9;
    padding: 4.8rem 0 9.6rem 0;
}
.heading-primary{
    font-weight: 700;
    color: #333;
    letter-spacing: -0.5px;

}
.heading-primary{
    line-height: 1.05;
    font-size: 5.2rem;
    margin-bottom: 3.2rem;
}
.hero{
    max-width: 130rem;
    margin: 0 auto;
    padding: 0 3.2rem;
    display: grid;
    grid-template-columns:  1fr 1fr;
    gap:9.6rem;
    align-items: center;
}

.hero-description{
    font-size: 2rem;
    line-height: 1.6;
    margin-bottom: 4.8rem;
}
.btn,
.btn:link,
.btn:visited{
    display: inline-block;
    font-size: 2rem;
    font-weight: 600;
    padding: 1.6rem 3.2rem;
    text-decoration: none;
    border-radius: 0.9rem;
    /* ანიმაცია */
    transition: background-color 0.3s; 
}
.btn--full:link,
.btn--full:visited{
    background-color: #e67e22;
    color: #fff;
}
.btn--full:hover,
.btn--full:active{
    background-color: #cf711f;

}
.btn--outline:link,
.btn--outline:visited{
    background-color: #fff;
    color: #555;
}
.btn--outline:hover,
.btn--outline:active{
    background-color: #fdf2e9;
    /* border: 3px solid #fff ; */
    /* ბორდერის გაკეთება შიგნიდან,ტრიუკი */
    box-shadow: inset 0 0 0 0.3rem #fff;
}
.hero-img{
    width: 100%;
}

.delivered-meals{
   display: flex;
   align-items: center;
   gap:1.6rem;
   margin-top: 8rem;
}
.delivered-imgs{
    display: flex;
}
.delivered-imgs img{
    height: 4.8rem;
    width: 4.8rem;
    border-radius: 50%;
    margin-right: -1.6rem;
    border: 3px solid #fdf2e9;
}
.delivered-imgs img:last-child{
    margin: 0;
}
.delivered-text{
    font-size: 1.8rem;
    font-weight: 600;
}
.delivered-text span {
    color:#cf711f;
    font-weight: 700;
}


